header {
	display: -ms-flexbox;
	display: flex;
	background-color: orangered;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
	color: white;
}

header * {
	margin: 15px 20px;
}

header a {
	cursor: pointer;
}

@media (max-width: 550px) {
	header h1 {
		font-weight: 500;
	}
	
	header {
		-ms-flex-direction: column;
		    flex-direction: column;
	}
	
	header * {
		margin-bottom: 5px;
	}
	
	header a {
		border-bottom: 2px solid white;
		margin-bottom: 20px;
	}
}


